import React from "react";
import PropTypes from "prop-types";
import {AppContext} from "../contexts/AppContext";

export class GoogleMap extends React.Component {
    constructor(props) {
        super(props);
        this.map = null;
        this.currentMarker = null;
        this.handleGeocodeResults = this.handleGeocodeResults.bind(this);
        this.handleGeocodeResultsOnClick = this.handleGeocodeResultsOnClick.bind(this);
    }

    handleGeocodeResults(results, status, onSelect) {
        const google = this.context.google;
        const map = this.map;
        if (status === google.maps.GeocoderStatus.OK && results[0]) {
            if (this.currentMarker) {
                this.currentMarker.setMap(null);
                this.currentMarker = null;
            }
            this.currentMarker = new google.maps.Marker({
                position: results[0].geometry.location,
                map: map
            });
            map.panTo(results[0].geometry.location);
            if (onSelect) {
                onSelect(results[0].formatted_address);
            }
        }
    }

    handleGeocodeResultsOnClick(results, status) {
        this.handleGeocodeResults(results, status, this.props.onSelect);
    }

    centerMap(address) {
        if (!this.map || !address)
            return;
        const geocoder = this.context.geocoder;
        geocoder.geocode({address: address}, this.handleGeocodeResults);
    }

    loadGoogleMap(center) {
        const google = this.context.google;
        const geocoder = this.context.geocoder;
        const map = new google.maps.Map(document.getElementById('google-map-view'), {
            center: center,
            zoom: 15
        });
        this.map = map;
        map.addListener('click', (ev) => {
            geocoder.geocode({latLng: ev.latLng}, this.handleGeocodeResultsOnClick);
        });
    }

    init(address) {
        const google = this.context.google;
        const geocoder = this.context.geocoder;
        // Use Montreal coordinates as default coordinates.
        let defaultCenter = new google.maps.LatLng(45.505331312, -73.55249779);
        if (address) {
            geocoder.geocode({
                address: address
            }, (results, status) => {
                if (status === google.maps.GeocoderStatus.OK && results[0]) {
                    defaultCenter = results[0].geometry.location;
                }
                this.loadGoogleMap(defaultCenter);
            });
        } else {
            this.loadGoogleMap(defaultCenter);
        }
    }

    render() {
        if (this.props.centerMap)
            this.centerMap(this.props.address);
        return (
            <div>
                <div id="google-map-view" className="my-4"/>
            </div>
        );
    }

    componentDidMount() {
        this.init(this.props.address);
    }
}

GoogleMap.contextType = AppContext;
GoogleMap.propTypes = {
    address: PropTypes.string.isRequired,
    onSelect: PropTypes.func.isRequired,
    centerMap: PropTypes.bool.isRequired,
};
